import type { Meta, StoryFn } from '@storybook/web-components';

import { html } from 'lit';

import { FieldSize } from '@tiger-analytics/ui';

export default {
  title: 'Form/ta-input',
  component: 'ta-input',
  parameters: {
    controls: { expanded: true },
  },
  argTypes: {
    id: {
      description: 'Mandatory - unique id for testing',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    label: {
      description: 'Label for the input',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    description: {
      description: 'Additional description for the field',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    value: {
      description: 'value of the input field',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    placeholder: {
      description: 'Placeholder text',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    type: {
      options: ['text', 'password', 'email', 'number'],
      description: 'type of the input',
      table: {
        defaultValue: { summary: 'text' },
      },
      control: { type: 'select' },
    },
    size: {
      options: [FieldSize.Default, FieldSize.Small],
      description: 'size of the input',
      table: {
        defaultValue: { summary: FieldSize.Default },
      },
      control: { type: 'select' },
    },
    disabled: {
      description: 'enable or disable the input',
      table: {
        defaultValue: { summary: 'false' },
      },
      control: 'boolean',
    },
    required: {
      description: 'mark input as required',
      table: {
        defaultValue: { summary: 'false' },
      },
      control: 'boolean',
    },
    minlength: {
      description: 'minimum length for the input',
      table: {
        defaultValue: { summary: '0' },
      },
      control: 'number',
    },
    maxlength: {
      description: 'maximum length for the input',
      table: {
        defaultValue: { summary: '524288' },
      },
      control: 'number',
    },
    pattern: {
      description: 'input pattern',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    input: {
      description: 'Triggered when the input value is modified',
      table: {
        category: 'Events',
      },
      control: false,
    },
    focus: {
      description: 'Focuses the text input',
      table: {
        category: 'Methods',
      },
      control: false,
    },
  },
} as Meta;

const description = `ta-input is a customizable input field component. It supports various input types such as text, password, email, and number, and allows customization of attributes like label, placeholder, size, and validation properties (e.g., required, minlength, maxlength, pattern). The component also includes support for descriptions, focus management, and input change handling, making it a versatile solution for forms and user input fields.`;

const Template: StoryFn = (args) => {
  const inputChangeHandler = (event: Event) => {
    const inputTarget = event.target as HTMLInputElement;
    const outputElement = document.getElementById('entered-text');
    if (outputElement) outputElement.innerHTML = inputTarget.value;
  };
  return html`
    <ta-grid-container .showSideMargin="${false}">
      <ta-grid-column>
        <ta-input
          id="${args.id}"
          .label="${args.label}"
          .size="${args.size}"
          ?disabled="${args.disabled}"
          .description="${args.description}"
          .type="${args.type}"
          .value="${args.value}"
          .placeholder="${args.placeholder}"
          .required="${args.required}"
          .minlength="${args.minlength}"
          .maxlength="${args.maxlength}"
          .pattern="${args.pattern}"
          @input="${(e: Event) => inputChangeHandler(e)}"></ta-input>
        <p>Entered text: <span id="entered-text"></span></p>
      </ta-grid-column>
      <ta-grid-column
        ><div class="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div></ta-grid-column
      >
      <ta-grid-column
        ><div class="story-description">${description}</div>
        <div class="story-description">
          <b>Note :</b>To group multiple input fields, fieldset should be used.
          <a
            href="https://developer.mozilla.org/en-US/docs/Web/HTML/Element/fieldset"
            target="_blank"
            >Read more about fieldset</a
          >
        </div></ta-grid-column
      >
    </ta-grid-container>
  `;
};

export const CodeAndAccessibility = Template.bind({});
CodeAndAccessibility.args = {
  id: 'test-input',
  label: 'Label',
  size: FieldSize.Default,
  disabled: false,
  description: 'Some description',
  type: 'text',
  value: '',
  placeholder: 'Enter text',
  required: false,
  minlength: 0,
  maxlength: 524288,
  pattern: '',
};
